import React, { useState, useEffect } from 'react'
import Note from './Notes'
import Notification from './Notification'
import noteService from '../noteService'

const App = () => {
  const [notes, setNotes] = useState()
  const [newNote, setNewnote] = useState('')
  const [showAll, setShowAll] = useState(true)
  const [errorMessage, setErrorMessage] = useState(null)


  const addNote = event => {
    console.log(notes)
    event.preventDefault()
    console.log('button clicked', event.target)

    if (newNote === "") {
      setNewnote('')
      setErrorMessage('Notes can not be empty')
        setTimeout(() => {
          setErrorMessage(null)
        }, 3000);
        return
    }

    setNotes(
      [...notes].concat({
        id: notes.length + 1,
        content: newNote,
        date: new Date(Date.now()).toString(),
        important: Math.random() < 0.5
      })
    )
    setNewnote('')
    setErrorMessage('note added')
      setTimeout(() => {
        setErrorMessage(null)
      }, 3000);

  }

  const inputHandler = e => {
    console.log(e.target.value)
    setNewnote(e.target.value)
  }

  const notesToShow = showAll
    ? notes
    : notes.filter(note => note.important === true)

  const toggleImportanceOf = id => {
    const note = notes.find(n => n.id === id)
    console.log(note)
    
    const changedNote = { ...note, important: !note.important }
    delete changedNote.id
    noteService
      .update(note.id,changedNote)
      .then(returnedNote => {
        setNotes(notes.map(note => (note.id !== id ? note : returnedNote)))
      })
      .catch(error => {
        setErrorMessage(
          error.message
        )
        setTimeout(() => {
          setErrorMessage(null)
        }, 5000)
        setNotes(notes.filter(n => n.id !== id))
      })
  }

  useEffect(() => {
    noteService.getAll().then(response => {
      console.log(response)
      setNotes(response.reverse())
    })
  }, [])

  return (
    <div>
      <Notification message={errorMessage} />
      <h1>Notes</h1>
      <div>
        <button onClick={() => setShowAll(!showAll)}>
          show {showAll ? 'important' : 'all'}
        </button>
      </div>
      <ul>
        {notesToShow && notesToShow.map(item => (
          <Note key={item.id} item={item} toggleImportanceOf={toggleImportanceOf} />
        ))}
      </ul>

      <form onSubmit={addNote}>
        <input value={newNote} onChange={e => inputHandler(e)} />
        <button type='submit'>save</button>
      </form>
    </div>
  )
}

export default App
